import styled from "styled-components";
import { DonaButton } from "./DonaButton";

const desktopIcons = [ "siteLink.png", "phone.png", "logo.png", "address.png" ];

const Container = styled.div`
    width: calc(100% - 40px);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
`;

const HeaderItem = styled.div`
    width: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    // &:first-of-type {
    //     justify-content: flex-start;
    // }
    // &:last-of-type {
    //     justify-content: flex-end;
    // }
    max-width: 50%;
`;

export function Header({ windowWidth, menuClick }) {
    
    let icons = desktopIcons;
    if (windowWidth < 620) icons = [ icons[2] ];
    else if (windowWidth < 1300) icons = icons.slice(1, 3);
    
    return (
        <Container>
            {icons.map((icon, i) => (
                <HeaderItem key={i}>
                    <img src={`/images/${icon}`} alt={icon.split('.')[0]} height={icons.length > 1 && i === Math.floor(icons.length / 2) ? 100: 50}/>
                </HeaderItem>
            ))}
            
            <HeaderItem>
                <DonaButton title={"MENU"} onClick={() => menuClick()}/>
            </HeaderItem>
        </Container>
    );
}
