import styled from "styled-components";

const Container = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: ${({ shifted }) => shifted ? '-100px': '0px'}
`;

const DonaImage = styled.img`
    width: ${({ size }) => size - 40}px;
    height: ${({ size }) => size - 40}px;
    border: 0px solid black;
    margin: 20px 20px;
`;


export function DonaRow({ donas, size, shifted }) {

    return (
        <Container shifted={shifted}>
            {donas.map((pic, i) => (
                <DonaImage key={i} size={size} src={`/donas/${pic}`} />
            ))}
        </Container>
    );
}
