import styled from "styled-components";

import { shuffle } from "../utils";

const Container = styled.div`
    position: fixed;
    bottom: -65px;
    width: 100%;
    display: flex;
    justify-content: space-between;
`;
const Cluster = styled.div`
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    &.mirrored {
        transform: scaleX(-1);
    }
`;
const DudeGif = styled.img`
    width: ${props => props.small ? '360px': '480px'};
    transform-origin: bottom;
    &:first-of-type {
        transform: scale(${props => props.small ? '0.8, 0.8': '0.67, 0.67'}) translate(0, -5px);
        margin-right: ${props => props.small ? '-280px': '-400px'};
    }
    &:last-of-type {
        transform: scale(${props => props.small ? '0.67, 0.67': '0.5, 0.5'}) translate(0, -10px);
        margin-left: ${props => props.small ? '-280px': '-400px'};
    }
`;

const dudes = [
    { file: "1.gif", rotate: -0.18 },
    { file: "2.gif", rotate: -0.6 },
    { file: "3.gif", rotate: 0 },
    { file: "4.gif", rotate: 0.1 },
    { file: "5.gif", rotate: -0.3 },
    { file: "6.gif", rotate: -0.1 }
];

function DudeCluster({ dudeArr = [], small = false, mirror = false }) {

    return <Cluster className={mirror ? 'mirrored': ''}>
        {dudeArr.map((dude, i) => (
            <DudeGif key={i} small={small} src={`/dudes/${dude.file}`} style={{ filter: `hue-rotate(${dude.rotate}turn)` }}/>
        ))}
    </Cluster>
}

export function Dudes({ windowWidth = window.innerWidth }) {
    const shuffledDudes = shuffle(dudes);

    return (
        <Container>
            <DudeCluster dudeArr={shuffledDudes.slice(0, 3)} small={windowWidth < 501}/>
            <DudeCluster dudeArr={shuffledDudes.slice(3)} small={windowWidth < 501} mirror={true}/>
        </Container>
    );
}
