import { useState } from "react";
import styled from "styled-components";
import { useWindowSize } from "@react-hook/window-size";

import { Header } from "./Header";
import { Countdown } from "./Countdown";
import { DonaButton } from "./DonaButton";
import { Dudes } from "./Dudes";
import { DonaRow } from "./DonaRow";
import { Menu } from "./Menu";

import { yellow } from "../colors";
import { galleryPics } from "../galleryPics";

import { shuffle } from '../utils';

const YellowBar = styled.div`
    background-color: ${yellow};
    color: #000;
    font-size: 20px;
    padding: 10px;
    text-align: center;
    border-bottom: 3px solid #000;
    overflow: hidden;
    position: relative;
`;

const MarqueeText = styled.div`
    white-space: nowrap;

    /* Starting position */
    -moz-transform:translateX(100%);
    -webkit-transform:translateX(100%);	
    transform:translateX(100%);

    /* Apply animation to this element */	
    -moz-animation: example1 15s linear infinite;
    -webkit-animation: example1 15s linear infinite;
    animation: example1 15s linear infinite;
    
    /* Move it (define the animation) */
    @-moz-keyframes example1 {
        0%   { -moz-transform: translateX(100%); }
        100% { -moz-transform: translateX(-100%); }
    }
    @-webkit-keyframes example1 {
        0%   { -webkit-transform: translateX(100%); }
        100% { -webkit-transform: translateX(-100%); }
    }
    @keyframes example1 {
        0%   { 
            -moz-transform: translateX(100%); /* Firefox bug fix */
            -webkit-transform: translateX(100%); /* Firefox bug fix */
            transform: translateX(100%); 		
        }
        100% { 
            -moz-transform: translateX(-100%); /* Firefox bug fix */
            -webkit-transform: translateX(-100%); /* Firefox bug fix */
            transform: translateX(-100%); 
        }
    }
`;

const Outer = styled.div`
    position: relative;
    height: calc(100vh - 34px);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
`;
const Gallery = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    overflow: hidden;
    background-color: #fff;
    //height: 100%;
`;

const GalleryGradient = styled.div`
    position: absolute;
    height: calc(100vh - 34px);
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 0;
    background: rgb(253, 45, 25);
    background: linear-gradient(180deg,
    rgba(253, 45, 25, 1) 0%,
    rgba(255, 255, 255, 0) 50%,
    rgba(0, 104, 255, 1) 100%);
`;

const FireImage = styled.img`
    width: 70%;
    max-width: 700px;
    margin-bottom: 5vh;
`;

const CenterBlock = styled.div`
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    padding: 20px;
`;

const shuffledGalleryPics = shuffle(galleryPics);

export function PreMiso() {
    const [ windowWidth, windowHeight ] = useWindowSize(0, 0, { fps: 1 });

    const [ donaMenu, setDonaMenu ] = useState(false);

    const donaTotalSize = (windowHeight - 34) / 3;

    const donaHorizontalCount = Math.floor(windowWidth / donaTotalSize) + 3;

    const rows = [];
    for (let rowIndex = 0; rowIndex < 3; rowIndex++) {
        rows[ rowIndex ] = shuffledGalleryPics.slice(donaHorizontalCount * rowIndex, donaHorizontalCount * (rowIndex + 1));
    }

    const countDownTimerFontSize = windowWidth < 880 ? `${windowWidth * 0.1}px` : '88px';

    if (donaMenu) return <Menu windowWidth={windowWidth} onClick={() => setDonaMenu(false)}/>

    return (
        <div>
            <YellowBar>
                <MarqueeText>COUNTDOWN TO FIRST OFFICIAL JAY PEGS NFT DROP: SEPTEMBER 13, 2021 AT 9:00PM EST</MarqueeText>
            </YellowBar>

            <Outer windowHeight={windowHeight}>
                <Gallery>
                    {rows.map((row, i) => (
                        <DonaRow key={i} donas={row} size={donaTotalSize} shifted={i % 2} />
                    ))}
                </Gallery>

                <GalleryGradient windowHeight={windowHeight}>
                    <Header windowWidth={windowWidth} menuClick={() => setDonaMenu(true)}/>

                    <CenterBlock>
                        <FireImage src={"/images/fire.png"}/>
                        <Countdown fontSize={countDownTimerFontSize}/>
                        <DonaButton title={"JOIN TELEGRAM"} onClick={() => window.open('https://t.me/joinchat/kjAtYO3LNA42YmY5', '_blank')}/>
                    </CenterBlock>

                    <Dudes windowWidth={windowWidth}/>
                </GalleryGradient>
            </Outer>
        </div>
    );
}
