import styled from "styled-components";
import ReactCountdown from "react-countdown";

const Container = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-style: normal;
    font-weight: 700;
    color: #fff;
    letter-spacing: 12px;
    text-shadow: -5px -5px 0 #000, 0 -5px 0 #000, 5px -5px 0 #000, 5px 0 0 #000, 5px 5px 0 #000, 0 5px 0 #000, -5px 5px 0 #000, -5px 0 0 #000, -10px -10px 5px #fbff00, 0 -10px 5px #fbff00, 10px -10px 5px #fbff00, 10px 0 5px #fbff00, 10px 10px 5px #fbff00, 0 10px 5px #fbff00, -10px 10px 5px #fbff00, -10px 0 5px #fbff00, -15px -15px 10px #fd7200, 0 -15px 10px #fd7200, 15px -15px 10px #fd7200, 15px 0 10px #fd7200, 15px 15px 10px #fd7200, 0 15px 10px #fd7200, -15px 15px 10px #fd7200, -15px 0 10px #fd7200;
    margin-top: -50px;
    margin-bottom: 0px;
    margin-right: -15px;
    transform: translateY(-10px);
    white-space: nowrap;
    padding: 10px;
    width: 90%;
`;

export function Countdown({ fontSize }) {

    return (
        <Container style={{ fontSize }}>
            <ReactCountdown date={Date.now() + 60 * 60 * 24 * 3 * 1000} renderer={({ formatted }) => (
                <>
                    {formatted.days}:{formatted.hours}:{formatted.minutes}:
                    {formatted.seconds}
                </>
            )}
            />
        </Container>
    );
}
